import React, { useState, useEffect } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  Typography,
  Stack,
  Alert,
  Snackbar,
  CircularProgress,
} from '@mui/material';

interface Attribute {
  id: number;
  name: string;
  description: string;
  format_data: string;
  is_required: boolean;
}

interface InstanceModalProps {
  isOpen: boolean;
  onClose: () => void;
  categoryId: number;
}

const InstanceModal: React.FC<InstanceModalProps> = ({
  isOpen,
  onClose,
  categoryId,
}) => {
  const [attributes, setAttributes] = useState<Attribute[]>([]);
  const [values, setValues] = useState<Record<number, string>>({});
  const [errors, setErrors] = useState<Record<number, string>>({});
  const [instanceName, setInstanceName] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [toast, setToast] = useState<{ open: boolean; message: string; severity: 'error' | 'success' }>({
    open: false,
    message: '',
    severity: 'error'
  });

  useEffect(() => {
    if (isOpen) {
      fetchAttributes();
      // Clear form when opening
      setInstanceName('');
      setValues({});
      setErrors({});
    }
  }, [isOpen, categoryId]);

  const fetchAttributes = async () => {
    try {
      const response = await fetch(`/api/categories/${categoryId}/attributes`);
      if (!response.ok) throw new Error('Failed to fetch attributes');
      const data = await response.json();
      setAttributes(data);
    } catch (error) {
      console.error('Error fetching attributes:', error);
      setToast({
        open: true,
        message: 'Failed to load attributes',
        severity: 'error'
      });
    }
  };

  const validateValue = (value: string, format: string): boolean => {
    if (!format) return true;
    try {
      const regex = new RegExp(format);
      return regex.test(value);
    } catch (e) {
      console.error('Invalid regex pattern:', format);
      return true;
    }
  };

  const handleChange = (attributeId: number, value: string, format: string) => {
    setValues((prev) => ({ ...prev, [attributeId]: value }));
    
    if (!validateValue(value, format)) {
      setErrors((prev) => ({
        ...prev,
        [attributeId]: `Value does not match format: ${format}`,
      }));
    } else {
      setErrors((prev) => {
        const newErrors = { ...prev };
        delete newErrors[attributeId];
        return newErrors;
      });
    }
  };

  const handleSubmit = async () => {
    try {
      setIsSubmitting(true);
      const instanceResponse = await fetch('/api/screwdrivers', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          name: instanceName,
          parent_id: categoryId,
          type: 'instance',
        }),
      });

      if (!instanceResponse.ok) {
        throw new Error('Failed to create instance');
      }

      const instance = await instanceResponse.json();

      const attributeValues = Object.entries(values).map(([attributeId, value]) => ({
        instance_id: instance.id,
        attribute_id: parseInt(attributeId),
        value,
      }));

      const valuesResponse = await fetch('/api/attribute-values/bulk', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ values: attributeValues }),
      });

      if (!valuesResponse.ok) {
        throw new Error('Failed to create attribute values');
      }

      setToast({
        open: true,
        message: 'Instance created successfully',
        severity: 'success'
      });

      // Clear form and close modal
      setInstanceName('');
      setValues({});
      setErrors({});
      onClose();
    } catch (error) {
      setToast({
        open: true,
        message: error instanceof Error ? error.message : 'Failed to create instance',
        severity: 'error'
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <>
      <Dialog 
        open={isOpen} 
        onClose={!isSubmitting ? onClose : undefined}
        maxWidth="md" 
        fullWidth
        disableEscapeKeyDown={isSubmitting}
        disableRestoreFocus
      >
        <DialogTitle>Create New Instance</DialogTitle>
        <DialogContent>
          <Stack spacing={3} sx={{ mt: 2 }}>
            <TextField
              required
              fullWidth
              label="Instance Name"
              value={instanceName}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => setInstanceName(e.target.value)}
              placeholder="Enter instance name"
              disabled={isSubmitting}
            />

            {attributes.map((attr) => (
              <div key={attr.id}>
                <TextField
                  fullWidth
                  required={attr.is_required}
                  error={!!errors[attr.id]}
                  helperText={errors[attr.id] || (attr.format_data ? `Format: ${attr.format_data}` : '')}
                  label={attr.name}
                  value={values[attr.id] || ''}
                  onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                    handleChange(attr.id, e.target.value, attr.format_data)
                  }
                  placeholder={`Enter ${attr.name}`}
                  disabled={isSubmitting}
                />
                <Typography variant="caption" color="text.secondary">
                  {attr.description}
                </Typography>
              </div>
            ))}
          </Stack>
        </DialogContent>
        <DialogActions>
          <Button onClick={onClose} disabled={isSubmitting}>Cancel</Button>
          <Button
            variant="contained"
            onClick={handleSubmit}
            disabled={
              isSubmitting ||
              !instanceName ||
              Object.keys(errors).length > 0 ||
              attributes.some((attr) => attr.is_required && !values[attr.id])
            }
            startIcon={isSubmitting ? <CircularProgress size={20} /> : null}
          >
            {isSubmitting ? 'Creating...' : 'Create Instance'}
          </Button>
        </DialogActions>
      </Dialog>

      <Snackbar
        open={toast.open}
        autoHideDuration={3000}
        onClose={() => setToast(prev => ({ ...prev, open: false }))}
      >
        <Alert severity={toast.severity}>
          {toast.message}
        </Alert>
      </Snackbar>
    </>
  );
};

export default InstanceModal; 