import React from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  Typography,
  Stack,
} from '@mui/material';
import { Instance } from '../../types';

interface AttributeModalProps {
  isOpen: boolean;
  onClose: () => void;
  instance: Instance;
}

const AttributeModal: React.FC<AttributeModalProps> = ({
  isOpen,
  onClose,
  instance,
}) => {
  if (!instance?.attributeValues) {
    return null;
  }

  return (
    <Dialog open={isOpen} onClose={onClose} maxWidth="md" fullWidth>
      <DialogTitle>Edit Attributes for {instance.name}</DialogTitle>
      <DialogContent>
        <Stack spacing={3} sx={{ mt: 2 }}>
          {instance.attributeValues.map((av) => (
            <div key={av.id}>
              <Typography variant="subtitle1">{av.attribute.name}</Typography>
              <TextField
                fullWidth
                value={av.value}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  // TODO: Implement attribute value update
                }}
                margin="normal"
              />
              <Typography variant="caption" color="text.secondary">
                {av.attribute.description}
              </Typography>
            </div>
          ))}
        </Stack>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}>Cancel</Button>
        <Button variant="contained" color="primary">
          Save Changes
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default AttributeModal; 