import React from 'react';
import {
  Box,
  Typography,
  Button,
  IconButton,
  Collapse,
  Stack,
  Card,
  CardContent,
  Tooltip,
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import DeleteIcon from '@mui/icons-material/Delete';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Category, Instance } from '../../types';
import InstanceCard from './InstanceCard';

interface CategoryCardProps {
  category: Category;
  onAddInstance: (categoryId: number) => void;
  onAddCategory: (parentId: number) => void;
  onDelete: (categoryId: number) => void;
  level?: number;
}

const CategoryCard: React.FC<CategoryCardProps> = ({
  category,
  onAddInstance,
  onAddCategory,
  onDelete,
  level = 0,
}) => {
  const [expanded, setExpanded] = React.useState(true);

  const renderChild = (child: Category | Instance, isLast: boolean) => {
    const commonStyles = {
      position: 'relative',
      '&:before': {
        content: '""',
        position: 'absolute',
        left: -16,
        top: 0,
        width: 16,
        height: 24,
        borderLeft: '2px solid #666',
        borderBottom: '2px solid #666',
      },
      '&:after': {
        content: '""',
        position: 'absolute',
        left: -16,
        top: 24,
        bottom: isLast ? 0 : -8,
        width: 2,
        background: isLast ? 'transparent' : '#666',
      },
    };

    if (child.type === 'category') {
      return (
        <Box key={child.id} sx={commonStyles}>
          <CategoryCard
            category={child as Category}
            onAddInstance={onAddInstance}
            onAddCategory={onAddCategory}
            onDelete={onDelete}
            level={level + 1}
          />
        </Box>
      );
    } else {
      return (
        <Box key={child.id} sx={commonStyles}>
          <InstanceCard instance={child as Instance} />
        </Box>
      );
    }
  };

  return (
    <Card sx={{ mb: 1, ml: level * 3 }}>
      <CardContent sx={{ py: 1 }}>
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <IconButton
              size="small"
              onClick={() => setExpanded(!expanded)}
              sx={{
                transform: expanded ? 'rotate(90deg)' : 'rotate(0deg)',
                transition: 'transform 0.2s',
              }}
            >
              <ExpandMoreIcon />
            </IconButton>
            <Typography variant="subtitle1">{category.name}</Typography>
          </Box>
          <Stack direction="row" spacing={1}>
            <Tooltip title="Add Instance">
              <Button
                variant="contained"
                size="small"
                startIcon={<AddIcon />}
                onClick={() => onAddInstance(category.id)}
              >
                Instance
              </Button>
            </Tooltip>
            <Tooltip title="Add Subcategory">
              <Button
                variant="outlined"
                size="small"
                startIcon={<AddIcon />}
                onClick={() => onAddCategory(category.id)}
              >
                Category
              </Button>
            </Tooltip>
            <Tooltip title="Delete Category">
              <IconButton
                size="small"
                color="error"
                onClick={() => onDelete(category.id)}
              >
                <DeleteIcon />
              </IconButton>
            </Tooltip>
          </Stack>
        </Box>
      </CardContent>
      <Collapse in={expanded}>
        <Box sx={{ py: 1, pl: 4 }}>
          {category.children?.map((child, index, array) => 
            renderChild(child, index === array.length - 1)
          )}
        </Box>
      </Collapse>
    </Card>
  );
};

export default CategoryCard; 