import React from 'react';
import {
  Box,
  Typography,
  IconButton,
  Button,
  Stack,
  Card,
  CardContent,
  CardActions,
  Tooltip,
} from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import InfoIcon from '@mui/icons-material/Info';
import { Instance } from '../../types';
import AttributeModal from '../Modals/AttributeModal';

interface AttributeValue {
  id: number;
  attribute_id: number;
  value: string;
  attribute: {
    name: string;
    is_required: boolean;
    format_data: string;
  };
}

interface InstanceCardProps {
  instance: Instance;
}

const InstanceCard: React.FC<InstanceCardProps> = ({ instance }) => {
  const [isOpen, setIsOpen] = React.useState(false);

  return (
    <Card sx={{ mb: 1 }}>
      <CardContent>
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
          <Typography variant="body2">{instance.name}</Typography>
          <Stack direction="row" spacing={1}>
            <Tooltip title="Edit Instance">
              <Button
                size="small"
                startIcon={<EditIcon />}
                onClick={() => setIsOpen(true)}
              >
                Edit
              </Button>
            </Tooltip>
            <Tooltip title="Delete Instance">
              <IconButton
                size="small"
                color="error"
              >
                <DeleteIcon />
              </IconButton>
            </Tooltip>
          </Stack>
        </Box>
      </CardContent>

      <AttributeModal
        isOpen={isOpen}
        onClose={() => setIsOpen(false)}
        instance={instance}
      />
    </Card>
  );
};

export default InstanceCard; 